.fade-in {
	opacity: 1;
	-webkit-animation-name: fadeInOpacity;
	        animation-name: fadeInOpacity;
	-webkit-animation-iteration-count: 1;
	        animation-iteration-count: 1;
	-webkit-animation-timing-function: ease-in;
	        animation-timing-function: ease-in;
	-webkit-animation-duration: 0.5s;
	        animation-duration: 0.5s;
}
@-webkit-keyframes fadeInOpacity {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
@keyframes fadeInOpacity {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
.project {
		display: flex;
		justify-content: center;
		align-items: center;
		border-bottom: solid 2px;
		margin-bottom: 3em;
		min-width: 500px;
		max-width: 500px;
		flex:1 1 auto;
		
	}

		.project .image {
			width:25%;
		}

			.project .image img {
				width: 100%;
				height: 50%
			}

		.project .content {
			padding: 2em 3em 0.1em 3em;
			width: 75%;
		}

		@media screen and (max-width: 980px) {

			.project .image {
				width: 55%;
				min-width: 0;
				min-width: initial;
				max-width: none;
				max-width: initial;
			}

			.project .content {
				padding: 1.5em 2em 0.1em 2em;
				width: 45%;
			}

		}

		@media screen and (max-width: 736px) {

			.project {
				display: block;
				margin-bottom: 2em;
				min-width: 0;
				min-width: initial;
				max-width: none;
				max-width: initial;

			}

				.project .image {
					width: 100%;
				}

				.project .content {
					width: 100%;
				}

		}

		@media screen and (max-width: 980px) {

			.project .content {
				padding: 1em 1.5em 0.1em 1.5em;
			}

		}

	.project {
		background-color: white;
		border-bottom-color: rgba(144, 144, 144, 0.25);
		position:relative;
	}

	.image {
		border-radius: 0;
		border: 0;
		display: inline-block;
		position: relative;
	}

		.image img {
			border-radius: 0;
			display: block;
		}

		.image.left, .image.right {
			max-width: 40%;
		}

			.image.left img, .image.right img {
				width: 100%;
			}

		.image.left {
			float: left;
			margin: 0 1.5em 1em 0;
			top: 0.25em;
		}

		.image.right {
			float: right;
			margin: 0 0 1em 1.5em;
			top: 0.25em;
		}

		.image.fit {
			display: block;
			margin: 0 0 2em 0;
			width: 100%;
		}

			.image.fit img {
				width: 100%;
			}

		.image.main {
			display: block;
			margin: 0 0 3em 0;
			width: 100%;
		}

			.image.main img {
				width: 100%;
			}

