.projects {
  display: grid;
  justify-content: center;
  align-content: center;
  grid-template-columns: repeat(3, 500px);
  grid-auto-rows: -webkit-min-content;
  grid-auto-rows: min-content;
  margin-top:20px;
}
@media only screen and (max-width: 768px) {
    /* For mobile phones: */
   .projects{
